"use client";

import { useState, useEffect } from 'react';

interface Task {
  id: string;
  title: string;
  description: string;
  completed: boolean;
  createdAt: string;
  priority: 'low' | 'medium' | 'high';
}

// SVG icons as React components
const PlusIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <line x1="12" y1="5" x2="12" y2="19"></line>
    <line x1="5" y1="12" x2="19" y2="12"></line>
  </svg>
);

const EditIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path>
    <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path>
  </svg>
);

const TrashIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <polyline points="3 6 5 6 21 6"></polyline>
    <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
    <line x1="10" y1="11" x2="10" y2="17"></line>
    <line x1="14" y1="11" x2="14" y2="17"></line>
  </svg>
);

const CheckIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <polyline points="20 6 9 17 4 12"></polyline>
  </svg>
);

const CloseIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <line x1="18" y1="6" x2="6" y2="18"></line>
    <line x1="6" y1="6" x2="18" y2="18"></line>
  </svg>
);

const CalendarIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <rect x="3" y="4" width="18" height="18" rx="2" ry="2"></rect>
    <line x1="16" y1="2" x2="16" y2="6"></line>
    <line x1="8" y1="2" x2="8" y2="6"></line>
    <line x1="3" y1="10" x2="21" y2="10"></line>
  </svg>
);

const SearchIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <circle cx="11" cy="11" r="8"></circle>
    <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
  </svg>
);

const StarIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon>
  </svg>
);

const ClockIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <circle cx="12" cy="12" r="10"></circle>
    <polyline points="12 6 12 12 16 14"></polyline>
  </svg>
);

const CircleIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <circle cx="12" cy="12" r="10"></circle>
  </svg>
);

const CheckCircleIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <path d="M22 11.08V12a10 10 0 1 1-5.93-9.14"></path>
    <polyline points="22 4 12 14.01 9 11.01"></polyline>
  </svg>
);

const SparklesIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <path d="M9.813 15.904 9 18.75l-.813-2.846a1.25 1.25 0 0 0-2.227 0L5.25 18.75l-2.846-.813a1.25 1.25 0 0 0 0-2.227L2.25 15l.813-2.846a1.25 1.25 0 0 0 2.227 0L6 15l2.846-.813a1.25 1.25 0 0 0 0-2.227L6 11.25l.813-2.846a1.25 1.25 0 0 0 2.227 0L9.75 11.25l2.846.813a1.25 1.25 0 0 0 0-2.227L9.75 9l.813-2.846a1.25 1.25 0 0 0-2.227 0L6.75 9l-2.846.813a1.25 1.25 0 0 0 0 2.227L6 12.75l-.813 2.846a1.25 1.25 0 0 0 2.227 0L9 12.75l2.846.813a1.25 1.25 0 0 0 0-2.227Z"></path>
    <path d="M16 3v10"></path>
    <path d="M21 3v10"></path>
    <path d="M19 5v6"></path>
  </svg>
);

const priorityConfig = {
  low: { 
    color: 'bg-emerald-100 text-emerald-700 border-emerald-200 hover:bg-emerald-200',
    icon: CircleIcon,
    gradient: 'from-emerald-400 to-teal-500'
  },
  medium: { 
    color: 'bg-amber-100 text-amber-700 border-amber-200 hover:bg-amber-200',
    icon: ClockIcon,
    gradient: 'from-amber-400 to-orange-500'
  },
  high: { 
    color: 'bg-rose-100 text-rose-700 border-rose-200 hover:bg-rose-200',
    icon: StarIcon,
    gradient: 'from-rose-400 to-pink-500'
  }
};

export default function TaskManager() {
  const [tasks, setTasks] = useState<Task[]>([]);
  const [newTask, setNewTask] = useState({ 
    title: '', 
    description: '', 
    priority: 'medium' as Task['priority'] 
  });
  const [editingTask, setEditingTask] = useState<Task | null>(null);
  const [filter, setFilter] = useState<'all' | 'pending' | 'completed'>('all');
  const [searchTerm, setSearchTerm] = useState('');

  // Load tasks from localStorage on component mount
  useEffect(() => {
    const savedTasks = localStorage.getItem('tasks');
    if (savedTasks) {
      try {
        setTasks(JSON.parse(savedTasks));
      } catch (e) {
        console.error('Failed to parse tasks from localStorage');
      }
    }
  }, []);

  // Save tasks to localStorage whenever tasks change
  useEffect(() => {
    localStorage.setItem('tasks', JSON.stringify(tasks));
  }, [tasks]);

  const addTask = () => {
    if (newTask.title.trim() === '') return;
    
    const task: Task = {
      id: Date.now().toString(),
      title: newTask.title,
      description: newTask.description,
      completed: false,
      createdAt: new Date().toISOString(),
      priority: newTask.priority
    };
    
    setTasks([task, ...tasks]);
    setNewTask({ title: '', description: '', priority: 'medium' });
  };

  const updateTask = (id: string, updates: Partial<Task>) => {
    setTasks(tasks.map(task => 
      task.id === id ? { ...task, ...updates } : task
    ));
  };

  const deleteTask = (id: string) => {
    setTasks(tasks.filter(task => task.id !== id));
  };

  const toggleComplete = (id: string) => {
    updateTask(id, { completed: !tasks.find(t => t.id === id)?.completed });
  };

  const startEdit = (task: Task) => {
    setEditingTask({ ...task });
  };

  const saveEdit = () => {
    if (!editingTask) return;
    updateTask(editingTask.id, editingTask);
    setEditingTask(null);
  };

  const cancelEdit = () => {
    setEditingTask(null);
  };

  const filteredTasks = tasks.filter(task => {
    const matchesFilter = filter === 'all' || 
      (filter === 'pending' && !task.completed) ||
      (filter === 'completed' && task.completed);
    
    const matchesSearch = task.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      task.description.toLowerCase().includes(searchTerm.toLowerCase());
    
    return matchesFilter && matchesSearch;
  });

  const completedCount = tasks.filter(task => task.completed).length;
  const totalCount = tasks.length;
  const progressPercentage = totalCount > 0 ? (completedCount / totalCount) * 100 : 0;

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50 to-indigo-100 relative overflow-hidden">
      {/* Background decorative elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className="absolute -top-40 -right-40 w-80 h-80 bg-gradient-to-br from-blue-400/20 to-purple-600/20 rounded-full blur-3xl"></div>
        <div className="absolute -bottom-40 -left-40 w-80 h-80 bg-gradient-to-tr from-emerald-400/20 to-blue-600/20 rounded-full blur-3xl"></div>
        <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-96 h-96 bg-gradient-to-r from-purple-400/10 to-pink-600/10 rounded-full blur-3xl"></div>
      </div>

      <div className="relative z-10 container mx-auto px-4 py-8 max-w-5xl">
        {/* Header */}
        <div className="text-center mb-12">
          <div className="inline-flex items-center justify-center w-16 h-16 bg-gradient-to-r from-blue-600 to-purple-600 rounded-2xl mb-6 shadow-lg">
            <CheckCircleIcon />
          </div>
          <h1 className="text-5xl font-bold bg-gradient-to-r from-gray-900 via-blue-800 to-purple-800 bg-clip-text text-transparent mb-4">
            Task Manager
          </h1>
          <p className="text-xl text-gray-600 mb-8 max-w-2xl mx-auto leading-relaxed">
            Transform your productivity with our beautifully designed task management system
          </p>
          
          {/* Progress Stats */}
          <div className="flex flex-wrap justify-center gap-4 mb-8">
            <div className="bg-white/80 backdrop-blur-sm rounded-2xl px-6 py-4 shadow-lg border border-white/20">
              <div className="text-2xl font-bold text-gray-800">{totalCount}</div>
              <div className="text-sm text-gray-600">Total Tasks</div>
            </div>
            <div className="bg-white/80 backdrop-blur-sm rounded-2xl px-6 py-4 shadow-lg border border-white/20">
              <div className="text-2xl font-bold text-emerald-600">{completedCount}</div>
              <div className="text-sm text-gray-600">Completed</div>
            </div>
            <div className="bg-white/80 backdrop-blur-sm rounded-2xl px-6 py-4 shadow-lg border border-white/20">
              <div className="text-2xl font-bold text-blue-600">{Math.round(progressPercentage)}%</div>
              <div className="text-sm text-gray-600">Progress</div>
            </div>
          </div>

          {/* Progress Bar */}
          {totalCount > 0 && (
            <div className="max-w-md mx-auto">
              <div className="bg-white/50 rounded-full h-3 overflow-hidden shadow-inner">
                <div 
                  className="h-full bg-gradient-to-r from-blue-500 to-emerald-500 rounded-full transition-all duration-700 ease-out"
                  style={{ width: `${progressPercentage}%` }}
                ></div>
              </div>
            </div>
          )}
        </div>

        {/* Add Task Form */}
        <div className="mb-10 shadow-2xl border-0 bg-white/90 backdrop-blur-sm overflow-hidden rounded-2xl">
          <div className="absolute inset-0 bg-gradient-to-r from-blue-600/5 to-purple-600/5"></div>
          <div className="relative p-6">
            <h2 className="text-2xl text-gray-800 flex items-center gap-3 font-bold mb-6">
              <div className="w-8 h-8 bg-gradient-to-r from-blue-500 to-purple-500 rounded-lg flex items-center justify-center">
                <PlusIcon />
              </div>
              Create New Task
            </h2>
            <div className="relative space-y-6">
              <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
                <div className="lg:col-span-2">
                  <input
                    placeholder="What needs to be done?"
                    value={newTask.title}
                    onChange={(e) => setNewTask({...newTask, title: e.target.value})}
                    className="w-full h-12 text-lg border border-gray-200 rounded-lg px-4 focus:outline-none focus:ring-2 focus:ring-blue-400/20 focus:border-blue-400 bg-white/70"
                    onKeyPress={(e) => e.key === 'Enter' && addTask()}
                  />
                </div>
                <div>
                  <select
                    value={newTask.priority}
                    onChange={(e) => setNewTask({...newTask, priority: e.target.value as Task['priority']})}
                    className="w-full h-12 px-4 text-lg border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400/20 focus:border-blue-400 bg-white/70"
                  >
                    <option value="low">🟢 Low Priority</option>
                    <option value="medium">🟡 Medium Priority</option>
                    <option value="high">🔴 High Priority</option>
                  </select>
                </div>
              </div>
              <div>
                <input
                  placeholder="Add a description (optional)..."
                  value={newTask.description}
                  onChange={(e) => setNewTask({...newTask, description: e.target.value})}
                  className="w-full h-12 text-lg border border-gray-200 rounded-lg px-4 focus:outline-none focus:ring-2 focus:ring-blue-400/20 focus:border-blue-400 bg-white/70"
                />
              </div>
              <button 
                onClick={addTask}
                disabled={!newTask.title.trim()}
                className={`w-full h-12 text-lg text-white shadow-lg hover:shadow-xl transition-all duration-200 rounded-lg
                  ${!newTask.title.trim() 
                    ? 'bg-gray-400 cursor-not-allowed' 
                    : 'bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 hover:scale-[1.02]'} 
                  flex items-center justify-center`}
              >
                <PlusIcon className="mr-2" />
                Add Task
              </button>
            </div>
          </div>
        </div>

        {/* Filter and Search */}
        <div className="mb-8 flex flex-col lg:flex-row gap-6 items-center justify-between">
          <div className="flex gap-2 bg-white/80 backdrop-blur-sm rounded-2xl p-2 shadow-lg">
            <button
              onClick={() => setFilter('all')}
              className={`rounded-xl transition-all duration-200 px-4 py-2
                ${filter === 'all' 
                  ? 'bg-gradient-to-r from-blue-600 to-purple-600 text-white shadow-lg' 
                  : 'hover:bg-gray-100'}`}
            >
              All <span className="ml-2 bg-white/20 rounded-full px-2 py-0.5">{totalCount}</span>
            </button>
            <button
              onClick={() => setFilter('pending')}
              className={`rounded-xl transition-all duration-200 px-4 py-2
                ${filter === 'pending' 
                  ? 'bg-gradient-to-r from-amber-500 to-orange-500 text-white shadow-lg' 
                  : 'hover:bg-gray-100'}`}
            >
              Pending <span className="ml-2 bg-white/20 rounded-full px-2 py-0.5">{totalCount - completedCount}</span>
            </button>
            <button
              onClick={() => setFilter('completed')}
              className={`rounded-xl transition-all duration-200 px-4 py-2
                ${filter === 'completed' 
                  ? 'bg-gradient-to-r from-emerald-500 to-teal-500 text-white shadow-lg' 
                  : 'hover:bg-gray-100'}`}
            >
              Completed <span className="ml-2 bg-white/20 rounded-full px-2 py-0.5">{completedCount}</span>
            </button>
          </div>
          <div className="relative w-full lg:w-80">
            <div className="absolute left-4 top-1/2 transform -translate-y-1/2 text-gray-400">
              <SearchIcon />
            </div>
            <input
              placeholder="Search tasks..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-12 h-12 text-lg bg-white/80 backdrop-blur-sm border border-gray-200 rounded-2xl shadow-lg px-4 focus:outline-none focus:ring-2 focus:ring-blue-400/20 focus:border-blue-400"
            />
          </div>
        </div>

        {/* Task List */}
        <div className="space-y-4">
          {filteredTasks.length === 0 ? (
            <div className="text-center py-16 border-0 bg-white/60 backdrop-blur-sm shadow-xl rounded-2xl">
              <div className="text-gray-500">
                {tasks.length === 0 ? (
                  <div className="space-y-4">
                    <div className="w-20 h-20 bg-gradient-to-r from-blue-400 to-purple-500 rounded-full flex items-center justify-center mx-auto">
                      <SparklesIcon />
                    </div>
                    <div>
                      <p className="text-2xl font-semibold text-gray-700 mb-2">Ready to get started?</p>
                      <p className="text-lg text-gray-500">Create your first task and begin your productivity journey!</p>
                    </div>
                  </div>
                ) : (
                  <div className="space-y-4">
                    <div className="w-20 h-20 bg-gradient-to-r from-gray-400 to-gray-500 rounded-full flex items-center justify-center mx-auto">
                      <SearchIcon />
                    </div>
                    <div>
                      <p className="text-2xl font-semibold text-gray-700 mb-2">No matching tasks</p>
                      <p className="text-lg text-gray-500">Try adjusting your search or filter criteria</p>
                    </div>
                  </div>
                )}
              </div>
            </div>
          ) : (
            filteredTasks.map((task, index) => {
              const PriorityIcon = priorityConfig[task.priority].icon;
              return (
                <div 
                  key={task.id} 
                  className={`group transition-all duration-300 hover:shadow-2xl border-0 transform hover:scale-[1.01] rounded-2xl
                    ${task.completed 
                      ? 'bg-gradient-to-r from-emerald-50/80 to-teal-50/80 backdrop-blur-sm' 
                      : 'bg-white/80 backdrop-blur-sm'
                    } shadow-lg hover:shadow-xl`}
                  style={{
                    animation: `fadeInUp 0.6s ease-out forwards`,
                    animationDelay: `${index * 100}ms`
                  }}
                >
                  <div className="p-6">
                    {editingTask?.id === task.id ? (
                      <div className="space-y-4">
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                          <input
                            value={editingTask.title}
                            onChange={(e) => setEditingTask({...editingTask, title: e.target.value})}
                            className="w-full h-12 text-lg border border-gray-200 rounded-lg px-4 focus:outline-none focus:ring-2 focus:ring-blue-400/20 focus:border-blue-400 bg-white"
                          />
                          <select
                            value={editingTask.priority}
                            onChange={(e) => setEditingTask({...editingTask, priority: e.target.value as Task['priority']})}
                            className="w-full h-12 px-4 text-lg border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-400/20 focus:border-blue-400 bg-white"
                          >
                            <option value="low">🟢 Low Priority</option>
                            <option value="medium">🟡 Medium Priority</option>
                            <option value="high">🔴 High Priority</option>
                          </select>
                        </div>
                        <input
                          value={editingTask.description}
                          onChange={(e) => setEditingTask({...editingTask, description: e.target.value})}
                          placeholder="Task description..."
                          className="w-full h-12 text-lg border border-gray-200 rounded-lg px-4 focus:outline-none focus:ring-2 focus:ring-blue-400/20 focus:border-blue-400 bg-white"
                        />
                        <div className="flex gap-3">
                          <button 
                            onClick={saveEdit} 
                            className="bg-gradient-to-r from-emerald-600 to-teal-600 hover:from-emerald-700 hover:to-teal-700 text-white shadow-lg rounded-lg px-4 py-2 flex items-center"
                          >
                            <CheckIcon className="mr-2" />
                            Save Changes
                          </button>
                          <button 
                            onClick={cancelEdit} 
                            className="border border-gray-300 hover:bg-gray-50 rounded-lg px-4 py-2 flex items-center"
                          >
                            <CloseIcon className="mr-2" />
                            Cancel
                          </button>
                        </div>
                      </div>
                    ) : (
                      <div className="flex items-start gap-4">
                        <div className="flex-shrink-0 mt-1">
                          <input
                            type="checkbox"
                            checked={task.completed}
                            onChange={() => toggleComplete(task.id)}
                            className="w-6 h-6 rounded-full border-2 border-gray-300 focus:ring-0 focus:ring-offset-0 
                              checked:bg-emerald-600 checked:border-emerald-600"
                          />
                        </div>
                        <div className="flex-1 min-w-0">
                          <div className="flex items-start justify-between gap-4">
                            <div className="flex-1">
                              <h3 className={`text-xl font-semibold mb-2 transition-all duration-200 ${
                                task.completed 
                                  ? 'line-through text-gray-500' 
                                  : 'text-gray-800 group-hover:text-gray-900'
                              }`}>
                                {task.title}
                              </h3>
                              {task.description && (
                                <p className={`text-base mb-3 transition-all duration-200 ${
                                  task.completed 
                                    ? 'line-through text-gray-400' 
                                    : 'text-gray-600'
                                }`}>
                                  {task.description}
                                </p>
                              )}
                              <div className="flex items-center gap-3 flex-wrap">
                                <span className={`text-sm px-3 py-1 rounded-full border transition-colors duration-200 ${priorityConfig[task.priority].color} flex items-center`}>
                                  <PriorityIcon className="mr-1" />
                                  {task.priority} priority
                                </span>
                                <div className="flex items-center gap-1 text-sm text-gray-500">
                                  <CalendarIcon className="mr-1" />
                                  {new Date(task.createdAt).toLocaleDateString('en-US', { 
                                    month: 'short', 
                                    day: 'numeric',
                                    year: 'numeric'
                                  })}
                                </div>
                              </div>
                            </div>
                            <div className="flex gap-2 flex-shrink-0 opacity-0 group-hover:opacity-100 transition-opacity duration-200">
                              <button
                                onClick={() => startEdit(task)}
                                className="h-10 w-10 p-0 border border-gray-200 hover:bg-blue-50 hover:border-blue-300 hover:text-blue-600 transition-all duration-200 rounded-lg flex items-center justify-center"
                              >
                                <EditIcon />
                              </button>
                              <button
                                onClick={() => deleteTask(task.id)}
                                className="h-10 w-10 p-0 border border-gray-200 hover:bg-red-50 hover:border-red-300 hover:text-red-600 transition-all duration-200 rounded-lg flex items-center justify-center"
                              >
                                <TrashIcon />
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              );
            })
          )}
        </div>

        {/* Footer */}
        <div className="mt-16 text-center">
          <div className="inline-flex items-center gap-2 text-gray-500 text-lg">
            <SparklesIcon />
            <span>Stay productive, stay organized</span>
            <SparklesIcon />
          </div>
        </div>
      </div>

      <style jsx>{`
        @keyframes fadeInUp {
          from {
            opacity: 0;
            transform: translateY(20px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
      `}</style>
    </div>
  );
}